import React from 'react';
import NigirisImg  from '../images/Nigiris.jpg';
require('../styles/CateringPage.css');

const  CateringPage = () => {
	const P1 = "Nikki's Restaurant & Sushi Bar is the perfect compliment to your special occasion. "+
	" Whether you are having a dinner party with your closest friends or planning a wedding for 300, Nikki's will add a touch of elegance to your gathering. ";

	const P2 = "Our on-site sushi chefs are perfect for large parties, bringing a unique flair of entertainment to any occasion.  For smaller groups,"+
	" Nikki's Restaurant & Sushi Bar has combination platters that offer a variety of rolls sure to please any crowd.";
	return ( 


			 <div className="row">
			 	<div className="col-sm-9 col-xs-12">
	 				 <div className = "CateringPage_About">
						 <p>{P1}</p>	
		 				 <p>{P2}</p>	
		 				 <p>For questions or to book an event, please contact us:<a href="mailto:gm.nikkissushibar@gmail.com">gm.nikkissushibar@gmail.com</a>	 </p>				 		
	 				</div>
	 				

			 	</div>


			 	<div className="col-sm-3 hidden-xs ">
			 		<img src={NigirisImg} className="img-responsive img-thumbnail"/>
			 	</div>





			 </div>

		);
}

export default CateringPage;


