import React from 'react';
import bootstrap from 'bootstrap';
import {Link} from 'react-router';
require('../styles/NavBar.css');
const NavBar = () => {

	return (
		<nav className="navbar navbar-inverse Nikkis-navbar">
	 

		    <div className="navbar-header ">
		    <div className="hidden-md  hidden-lg hidden-sm navbar-brand Nikkis-navbar-brand" > <label>Welcome! </label></div>
		      <button type="button" className="navbar-toggle  collapsed" data-toggle="collapse" data-target="#myNavbar" aria-expanded="false">
		        <span className="sr-only">Toggle navigation</span>
		        <span className="icon-bar"></span>
		        <span className="icon-bar"></span>
		        <span className="icon-bar"></span>
		      </button>
		    </div>

		    <div className="collapse navbar-collapse   Nikkis-navbar-collapse" id="myNavbar">
	    	  <ul className="nav navbar-nav">
		       	<li className="  Nikkis-navbar-collapse-item ">
		       		<Link to="/home" activeClassName="navbar--active  "> HOME </Link> 
		         </li>
		         <li className="  Nikkis-navbar-collapse-item ">
		        	 <Link to="/about" activeClassName="navbar--active   "> 
		        	 		ABOUT </Link> 
		         </li>
				     <li className="  Nikkis-navbar-collapse-item ">
		        				<a href="#">MENUS</a>
		        </li> 
		        <li className="  Nikkis-navbar-collapse-item "> 
		        			<a href="#">SUSHI GALLERY</a></li> 
			    <li className="  Nikkis-navbar-collapse-item ">
			    		 <Link to="/catering" activeClassName="navbar--active   "> CATERING </Link>  </li>
			    <li className="  Nikkis-navbar-collapse-item ">
			    		<Link to="/locations" activeClassName="navbar--active" > LOCATIONS </Link>  </li>
		        <li className="  Nikkis-navbar-collapse-item ">
		        	<a href="#">CONTACT US</a></li> 
	      </ul>
	    </div>
	 
  </nav>


		);

}

export default NavBar;


