import React from 'react';
import NinjaRollImage from '../images/NinjaRoll.jpg';
require("../styles/AboutPage.css");
const AboutPage = () => {

	 const AboutP1 = "Nikki’s Restaurant &amp; Sushi Bar opened its first location on Front St. in downtown Wilmington,"+
	 "North Carolina in 2003. Brothers Andy and Johnny Chen opened the location after spending many years studying "+
	 "the art of sushi along the entire east coast, and realizing the large demand for their high quality product in the Wilmington area.";

	 const AboutP2 ="Soon after reaching success with the downtown location, the Chen’s decided to open a second in Independence Mall."+ 
	 "The menu remained the same, including many vegetarian dishes that quickly received awards and recognition from area publications.";

	 const AboutP3 = "In 2007, the brothers decided to provide a new menu and a unique atmosphere by opening Nikki’s Hibachi Steakhouse, "+
	 "a hibachi restaurant located on Military Cutoff Road.&nbs. Our talented chef’s and dazzling cocktails offer a memorable experience perfect for all occasions.";

	 const AboutP4 = "Located in Racine commons, Nikki’s @ Racine is located close to the University of North Carolina Wilmington, and offers the first full "+
	 "service bar out of all the restaurants. Nikki’s Sushi Bar’s diverse menu focuses on obtaining the freshest seafood and the finest ingredients available."+ 
	 "Our menus have become a favorite for Wilmington locals and tourists alike, offering traditional Japanese fare along with Mediterranean, Greek and classical American items. ";

	 const AboutP5 = "In addition to our unique entrees, Nikki’s sushi chef’s are dedicated to providing the freshest fish possible for our incredible sushi dishes. Specials"+
	 " are created daily in fun and flavorful combinations, ranging from nigiri and sashimi to rolls wrapped in cucumber.";

	 const AboutP6 = "We invite you to visit any of our five locations in North Carolina.";

	return(

			 <div className="row">
			 	<div className="  col-sm-9 col-xs-12">
			 	<div className="AboutPage_About">
				 		<p> {AboutP1} </p>
				 		<p> {AboutP2} </p>
				 		<p> {AboutP3} </p>
				 		<p> {AboutP4} </p>	
				 		<p> {AboutP5} </p>
				 		<p> {AboutP6} </p>			
			 		</div> 				 		

			 	</div>


			 	<div className="col-sm-3 hidden-xs ">
			 		<img src={NinjaRollImage} className="img-responsive AboutPage_img img-thumbnail"/>
			 	</div>





			 </div>
		);

}

export default AboutPage;


