import React from 'react';

/*		this.storeName = storeName;
		this.locationName = locationName;
		this.street = street;
		this.city = city;
		this.state = state;
		this.zipcode = zipcode;
		this.phone = phone;
		this.mapUrl = mapUrl;*/
const LocationPanel = ({location})=>{
	console.log(location);
	return (
			<div  className="Location_Panel row">
					<div className="col-xs-12 col-md-6">
						<h5> {  location.locationName}  </h5>

						<button type="button" className = "btn btn-default"> 
							VIEW MENU
						</button>


						<a href={location.mapUrl}   className = "btn btn-default"> 
							VIEW MAP
						</a>

					 </div>

					<div className="col-xs-12 col-md-6">

						<label>{location.storeName }</label>
						<br/>
						<label>{location.street }</label>
						<br/>
						<label>{location.city+", "+ location.state + " " + location.zipcode  }</label>
						<br/>
						<label>{"Phone: "+ location.phone }</label>
						<br/>

					 </div>
			

			</div>
	);

}

export default LocationPanel;


