import React from 'react';
import {Link} from 'react-router';
import TinyFish from '../images/tinyfish.png';
require("../styles/HomePage.css");
const HomePage = () => {
	let p1 = '"Nikki’s Sushi Bar serves diners all across New Hanover County, from downtown to midtown (including their mall and Racine Drive locations). '+
	'They’re newest location in Carolina Beach keeps folks in the southern part of town happily sated, while their sushi bar and Japanese steak house allow  '+
	' beach-goers toward Wrightsville a flavor of excellence.';

	let p2 = 'Owned and operated by Johnny Chen, Nikki’s specializes in many famed rolls, like the Fantasy Roll, featuring tempura shrimp, eel, avocado and scallions'+
	' topped with fish roe and tempura flakes. Folks looking for interesting and even healthier takes can opt for one of their naruto rolls, where the fish, avocado and'+
	' other accompaniments are wrapped in cucumber instead of rice and seaweed and served in a delightful and tangy vinegar-based sauce."';

	let p3 = '       - Encore Magazine';
  return (
    <div className="row">
    	
    	<div className="col-xs-12  col-sm-9 ">
    		<div className="HomePage_Welcome">
    			<img src={TinyFish} className="HomePage_Welcome_Image"/>
    			<div className="HomePage_Welcome_Text">
    				<h2 className="HomePage_Welcome_Text_Head">Nikki's Sushi Welcomes You</h2>
    				<p className="HomePage_Welcome_Text_P"> 
    					{p1}
    				</p>
    				<p className="HomePage_Welcome_Text_P"> 
    				{p2}
    				</p>
    			    <p className="HomePage_Welcome_Text_P"> 
    					{p3}
    				</p>
    			</div>
    		</div>

            <div className="embed-responsive embed-responsive-16by9">
                <iframe className="embed-responsive-item"  src="https://www.youtube.com/embed/muGhQSZ6BlA" allowfullscreen></iframe>
            </div>
		</div>
    	<div className="col-xs-12  col-sm-3 ">
    				<div className="HomePage_Special">
    					<h3 className="HomePage_Special_H">
    						Our Specials:
    					</h3>
                         <br />
    					<p className="text-black">
    						<span >
    							<strong> BIRTHDAY SPECIAL</strong>
                         
                                <a title="Military Cutoff" href="/index.php/our-locations-2/military-c/">HIBACHI</a>
    							- Dine for free when you bring in at least 4 friends on your Birthday, including up to 5 days before or 5 days after.
    						</span>
    					</p>
                        <p className="text-black">
    						<span><strong>HAPPY HOUR</strong>
                                    <br/>
                                                                      Join us at our 
                                <strong><a title="Racine" href="/index.php/our-locations-2/racine/"> Racine </a></strong>
                                 and
                                    <strong>  <a title="Independence Mall" href="/index.php/our-locations-2/i/">Mall</a> </strong>
    							  locations and enjoy our extensive happy hour menu and daily drink specials. 
                                  Offered at Racine Location Sunday - 
    							  Thursday from 4-6 p.m and our Independence Mall location from 
    							  <span >Sunday - Thursday from 2-4 p.m.</span>.
    							   Specials include half priced sushi, sashimi, and regular rolls along with select $3.00 appetizers.
    							   </span>
    					</p>
    				</div>
		</div>
    </div>
  );
};

export default HomePage;



