class LocationModel {
	constructor( storeName,  locationName ,  street , city, state, zipcode, phone, mapUrl ) {
		this.storeName = storeName;
		this.locationName = locationName;
		this.street = street;
		this.city = city;
		this.state = state;
		this.zipcode = zipcode;
		this.phone = phone;
		this.mapUrl = mapUrl;
		}
}


const locations = [];
function GetLocations(){
	if(locations.length == 0 ){
		const Mall = new LocationModel(
				"Independence Mall",
				"Nikki’s Gourmet & Sushi",
				"3520 Oleander Dr.",
				"Wilmington",
				"NC",
				"28403",
				"910.791.8887",
				"https://www.google.com/maps/place/3520+Oleander+Dr,+Wilmington,+NC+28403/@34.2162965,-77.907152,17z/data=!3m1!4b1!4m2!3m1!1s0x89a9f5a022e6052b:0xa7773ad1caa1268b"
			);
		const Downtown = new LocationModel(
				"Downtown",
				"Nikki’s Fresh Gourmet & Sushi",
				"16 South Front St.",
				"Wilmington",
				"NC",
				"28401",
				"910.772.9151",
				"https://maps.google.com/maps?q=16+S+Front+St+Wilmington,+NC+28401&hl=en&sll=34.226564,-77.887573&sspn=0.214035,0.388298&hnear=16+S+Front+St,+Wilmington,+New+Hanover,+North+Carolina+28401&t=m&z=17"
			);
		const Racine = new LocationModel(
				"Racine Commons",
				"Nikki's Sushi",
				"260 Racine Drive",
				"Wilmington",
				"NC",
				"28403",
				"910.799.6799",
				"https://maps.google.com/maps?q=260+Racine+Drive+Wilmington,+NC+28403&hl=en&sll=34.216283,-77.904972&sspn=0.006689,0.012134&hnear=260+Racine+Dr,+Wilmington,+New+Hanover,+North+Carolina+28403&t=m&z=17"
			);
		const Military = new LocationModel(
				"Military Cutoff",
				"Nikki’s Hibachi Steakhouse & Sushi Bar",
				"1055 Military Cutoff Rd., Suite 100",
				"Wilmington",
				"NC",
				"28405",
				"910.509.8998",
				"https://maps.google.com/maps?q=1055+Military+Cutoff+Rd.,+Suite+100+Wilmington,+NC+28405&hl=en&sll=34.238315,-77.867503&sspn=0.006688,0.012134&hnear=1055+Military+Cutoff+Rd+%23100,+Wilmington,+North+Carolina+28405&t=m&z=17"
			);
		const Carolina = new LocationModel(
				"Carolina Beach",
				"NNikki’s Sushi Restaurant",
				"6 South Lake Park Blvd.",
				"Carolina Beach,",
				"NC",
				"28428",
				"910.707.0802",
				"https://maps.google.com/maps?q=6+South+Lake+Park+Blvd.+Carolina+Beach,+NC+28428&hl=en&sll=34.427743,-77.544026&sspn=0.006673,0.012134&hnear=6+S+Lake+Park+Blvd,+Carolina+Beach,+New+Hanover,+North+Carolina+28428&t=m&z=17"
			);
		locations.push(Mall);
		locations.push(Downtown);
		locations.push(Racine);
		locations.push(Military);
		locations.push(Carolina);
	}
	const promise = new Promise((resolve,reject)=>{
			if(locations.length > 0){
				resolve(locations);
			}
			else{

				reject();
			}

	});

	return  promise;
}



const LocationApi = {
	GetLocations


};
export  default LocationApi;




